	data() {
		return {
			headerPosition: 'fixed',
			headerTop: '0px',
			typeText: {
				unpaid: '等待付款',
				back: '等待商家发货',
				unreceived: '商家正在配送',
				// received:'等待用户评价',
				completed: '交易已完成',
				refunds: '订单已取消'
			},
			orderType: ['全部', '待付款', '待发货', '待收货', '已完成', '已取消'],
			//订单列表 演示数据
			orderList: [[], [], [], [], [], []],
			list: [],
			tabbarIndex: 0
		};
	},
	onLoad(option) {
		//option为object类型，会序列化上个页面传递的参数
		console.log('option: ' + JSON.stringify(option));
		let tbIndex = parseInt(option.tbIndex) + 1;
		// this.list = this.orderList[tbIndex];
		this.tabbarIndex = tbIndex;
		console.log(tbIndex);
		this.showType(tbIndex);
		// this.loading(tbIndex);
		//兼容H5下排序栏位置









	},
	onPageScroll(e) {
		return;
		//兼容iOS端下拉时顶部漂移
		this.headerPosition = e.scrollTop >= 0 ? 'fixed' : 'absolute';
	},
	methods: {
		refreshList(tbIndex) {
			uni.request({
				url: this.$tempUrl + 'buyer/order/listByStatus',
				method: 'GET',
				data: { openId: uni.getStorageSync('openId'), status: tbIndex - 1 },
				success: res => {
					this.orderList[tbIndex] = res.data.data;
					this.list = this.orderList[tbIndex];
				}
			});
		},
		refreshListOther(tbIndex) {
			uni.request({
				url: this.$tempUrl + 'buyer/order/listByStatus',
				method: 'GET',
				data: { openId: uni.getStorageSync('openId'), status: tbIndex - 1 },
				success: res => {
					this.orderList[tbIndex] = res.data.data;
				}
			});
		},
		showType(tbIndex) {
			this.tabbarIndex = tbIndex;
			if (this.orderList[tbIndex].length === 0) {
				uni.request({
					url: this.$tempUrl + 'buyer/order/listByStatus',
					method: 'GET',
					data: { openId: uni.getStorageSync('openId'), status: tbIndex - 1 },
					success: res => {
						this.orderList[tbIndex] = res.data.data;
						this.list = this.orderList[tbIndex];
					}
				});
			} else {
				this.list = this.orderList[tbIndex];
			}
		},
		changeType(orderId, type) {
			console.log(orderId);
			if (type === 'unpaid') {
				uni.showModal({
					title: '提示',
					content: '取消此订单？',
					success: res => {
						if (res.confirm) {
							console.log('用户点击确定');
							uni.request({
								url: this.$tempUrl + 'buyer/order/cancel',
								method: 'POST',
								data: { openId: uni.getStorageSync('openId'), orderId: orderId },
								success: res => {
									if (res.data.code === 0) {
										uni.showToast({
											title: '订单已取消',
											icon: 'success'
										});
										//刷新当前（待付款）列表
										this.refreshList(1);
										//刷新已取消列表
										this.refreshListOther(5);
										//刷新全部列表
										this.refreshListOther(0);
									} else {
										uni.showModal({
											title: '提示',
											content: '订单取消失败',
											showCancel: false
										});
										//刷新当前（待付款）列表
										this.refreshList(1);
										//刷新全部列表
										this.refreshListOther(0);
									}
								}
							});
						} else if (res.cancel) {
							console.log('用户点击取消');
						}
					}
				});
			} else if (type === 'back') {
				uni.showModal({
					title: '提示',
					content: '取消此订单？',
					success: res => {
						if (res.confirm) {
							console.log('用户点击确定');
							uni.request({
								url: this.$tempUrl + 'buyer/order/cancel',
								method: 'POST',
								data: { openId: uni.getStorageSync('openId'), orderId: orderId },
								success: res => {
									if (res.data.code === 0) {
										uni.showToast({
											title: '订单已取消',
											icon: 'success'
										});
										//刷新当前（待发货）列表
										this.refreshList(2);
										//刷新已取消列表
										this.refreshListOther(5);
										//刷新全部列表
										this.refreshListOther(0);
									} else {
										uni.showModal({
											title: '提示',
											content: '商家已接单，订单取消失败',
											showCancel: false
										});
										//刷新当前（待发货）列表
										this.refreshList(2);
										//刷新待收货列表
										this.refreshListOther(3);
										//刷新全部列表
										this.refreshListOther(0);
									}
								}
							});
						} else if (res.cancel) {
							console.log('用户点击取消');
						}
					}
				});
			} else if (type === 'unreceived') {
				uni.showModal({
					title: '提示',
					content: '完结此订单？',
					success: res => {
						if (res.confirm) {
							console.log('用户点击确定');
							uni.request({
								url: this.$tempUrl + 'buyer/order/finish',
								method: 'POST',
								data: { openId: uni.getStorageSync('openId'), orderId: orderId },
								success: res => {
									if (res.data.code === 0) {
										uni.showToast({
											title: '订单已完结',
											icon: 'success'
										});
										this.refreshList(3);
										this.refreshListOther(4);
										//刷新全部列表
										this.refreshListOther(0);
									} else {
										uni.showModal({
											title: '提示',
											content: '订单完结失败',
											showCancel: false
										});
										this.refreshList(3);
										//刷新全部列表
										this.refreshListOther(0);
									}
								}
							});
						} else if (res.cancel) {
							console.log('用户点击取消');
						}
					}
				});
			}
		},
		//跳转确认订单页面
		toConfirmation(row) {
			uni.setStorageSync('shopId', row.shopId);
			uni.setStorage({
				key: 'buylist',
				data: row.orderDetailModels,
				success: () => {
					uni.navigateTo({
						url: '../../order/confirmation'
					});
				}
			});
		},
		// 拨打电话
		toCall(phoneNumber) {
			uni.makePhoneCall({
				phoneNumber: phoneNumber
			});
		},
		payment(order) {
			uni.showModal({
				title: '支付',
				content: '确认支付￥' + order.payableAmount,
				success: res => {
					if (res.confirm) {
						uni.request({
							url: this.$tempUrl + 'buyer/order/paid',
							method: 'POST',
							data: { openId: uni.getStorageSync('openId'), orderId: order.id },
							success: res => {
								if (res.data.code === 0) {
									// uni.reLaunch({
									// 	url: '../tabBar/user',
									// 	success: () => {
									// 		uni.showToast({ title: '支付成功', icon: 'success' });
									// 	}
									// });
									uni.showToast({ title: '支付成功', icon: 'success' });
									//刷新当前（待付款）列表
									this.refreshList(1);
									//刷新已发货列表
									this.refreshListOther(2);
									//刷新全部列表
									this.refreshListOther(0);
								} else {
									uni.showModal({
										title: '提示',
										content: '订单异常，支付失败',
										showCancel: false
									});
									//刷新当前（待付款）列表
									this.refreshList(1);
									//刷新全部列表
									this.refreshListOther(0);
								}
							}
						});
					} else if (res.cancel) {
						// uni.reLaunch({
						// 	url: '../tabBar/user',
						// 	success: () => {
						// 		uni.showToast({ title: '支付已取消，请尽快完成付款', icon: 'none' });
						// 	}
						// });
						uni.showToast({ title: '支付已取消，请尽快完成付款', icon: 'none' });
					}
				}
			});
		}
	}
};
